#contacts .boxleft{
	border: 5px solid #ffc070;
	border-radius:30px;
	background-color:#ffc070;
	position:absolute;
	top:45%;
	left:10%;
	width:80%;
	height:15%;
	text-align:center;
}
#contacts .left{
	all:initial;
}


#contacts .right{
	all:initial;
}
#contacts .boxcontainer{
	display: flex;
}
#contacts .boxright{
	border: 5px solid #ffc070;
	border-radius:30px;
	background-color:#ffc070;
	position:absolute;
	top:65%;
	left:10%;
	width:80%;
	height:15%;
	text-align:center;
}

#contacts .borderposition{
	width:80%;
	left:10%;
}
#contacts .border{
	font-size:6vw;
}
#contacts .email{
	display:none;
}
#contacts .locate{
	display:none;
}
#contacts .boxleftspan {
	font-size: 4vw;
}
#contacts .boxrightspan {
	font-size: 4vw;
}
#contacts .spanpostion{
	top:45%;
}



